.scrollTop {
    position: fixed; 
    bottom: 20px;
    right:20px;
    font-size:3rem;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.75;
    color:hsl(207, 26%, 17%);
  }
  .scrollTop.dark-arrow{
    color:hsl(0, 0%, 100%);
  }
  .scrollTop:hover{
    opacity: 1;
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }