@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;600;800&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: hsl(0, 0%,98%);
  font-family: 'Nunito Sans', sans-serif;
  font-display: swap;
  scroll-behavior: smooth;
}


.App {
  overflow-x: hidden;
  overflow-y: hidden;
  min-height:100vh;
  /*background:#f5f5f5;
  background:hsl(207, 26%, 17%); */
}
.dark-App{
  color:hsl(0, 0%, 100%);
  background:hsl(207, 26%, 17%);
}
.page{
  position: fixed;
  left:0;
  right:0;
}

.headerContainer,.buttonContainer{
    display: flex;
    flex-wrap: nowrap;
}
.headerContainer{
    justify-content: space-between;
}
.title{
    font-weight:800;
    font-size:1.5rem;
    margin-left:75px;
    margin-top:20px;
}
.buttonContainer{
    margin-top:7.5px;
    font-size:18px;
    margin-right:75px;
    font-weight:400;
    cursor: pointer;
}
.darkmode-button{
    cursor: pointer;
    font-weight:lighter;
    outline:none;
    border:none;
    background:rgb(0 0 0 /0);
    font-size:20px;
    margin-bottom:5px;
    margin-right:5px;
}
.Header{
  box-shadow: 0px 1px 0px 1px rgba(194,194,194,0.43);
}
.dark,.darkButton{
    color: white;
    background:#2b3743;
}
.dark{
    box-shadow: 0px 3px 0px 1px rgba(29,42,51,1);
}
@media screen and (max-width: 700px){
    .title{
        margin-left:50px;
        font-size:1.25rem;
    }
    .buttonContainer{
        margin-right:50px;
        font-size:16px;
    }
}
@media screen and (max-width:475px){
    .title{
        margin-left:25px;
        font-size:1rem;
    }
    .buttonContainer{
        margin-right:25px;
        font-size:13px;
    }
}
@media screen and (max-width:320px){
    .title{
        margin-left:15px;
        font-size:.8rem;
    }
    .buttonContainer{
        margin-right:15px;
        font-size:12px;
    }
    .darkmode-button{
        font-size:15px;
        margin-right:2px;
        margin-bottom:2.5px;
    }
}
.searchBar input{
    background:transparent;
    outline:none;
    border:none;
    font-size:1rem;
    position: relative;
    bottom:15px;
    width:600px;
    color:hsl(200, 15%, 8%);
}
.searchBar{
    border-radius:8px;
    width:auto;
    margin-left:50px;
    padding: 10px 25px;
    box-shadow: 1px 1px 7px 2px rgba(214,214,214,1);
    height:35px;
}
.searchIcon{
    color:lightslategray;
    font-size:25px;
    margin:7px;
    position: relative;
    cursor: pointer;
}
.selectRegion{
    border-radius:8px;
    margin-right:50px;
    box-shadow: 1px 1px 7px 2px rgba(214,214,214,1);
}
select{
    cursor:pointer;
    outline:none;
    border:none;
    border-radius:8px;
    min-width:195px;
    padding-left:15px;
    padding-right:20px;
    background:transparent;
    font-family: 'Nunito Sans', sans-serif;
    bottom:-15px;
    font-size:1rem;
    height:auto;
    overflow:hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.FilterContainer{
    display:flex;
    justify-content:space-between;
    margin-top:60px;
}
.dark-searchBar,.dark-selectRegion,.dark-searchBar input,.dark-searchIcon{
    background:#2b3743;
    color:hsl(0, 0%, 100%);
    box-shadow: none;    
}
.dark-searchBar input::placeholder{
    color:rgb(160, 163, 167);
}

@media screen and (max-width:1075px){
    .FilterContainer{
        margin-top:20px;
        margin-bottom:5px;
    }
    .searchBar input{
        width:450px;
    }
    .searchBar{
        margin-left:25px;
    }
    .selectRegion{
        margin-right:25px;
        min-width:175px;
    }
}
@media screen and (max-width:800px){
    .searchBar input{
        width:300px;
        bottom:20px;
    }
    .searchIcon{
        bottom:5px;
    }
    .searchBar{
        height:25px;
    }
}
@media screen and (max-width:650px){
    .FilterContainer{
        flex-direction: column;
    }
    .searchBar{
        margin:0 15px;
        padding:0;
        padding-bottom:12px;
    }
    .searchBar input{
        font-size:.85rem;
        width:415px;
        bottom:15px;
    }
    .searchIcon{
        bottom:0px;
    }
    .selectRegion{
        margin:25px 25px 0 15px;
        height:35px;
    }
    select{
        max-width:75px;
        height:35px;
        font-size:.8rem;
    }
}
@media screen and (max-width:490px){
    .searchBar input{
        width:250px;
    }
}
@media screen and (max-width:320px){
    .searchBar input{
        width:200px;
    }
}
.scrollTop {
    position: fixed; 
    bottom: 20px;
    right:20px;
    font-size:3rem;
    z-index: 1000;
    cursor: pointer;
    animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.75;
    color:hsl(207, 26%, 17%);
  }
  .scrollTop.dark-arrow{
    color:hsl(0, 0%, 100%);
  }
  .scrollTop:hover{
    opacity: 1;
  }
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
